@use "variables" as v;
html{
  font-size: 10px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*,*::before, *::after{
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body{
  font-family: v.$fuenteSecundaria;
  font-size: 1.6rem;
  margin: 0;
  padding: 0;
  
}

p{
  color: v.$verdeOscuro;
  font-size: 1.7rem;
  line-height: 1.8;
}

a{
  text-decoration: none;
}